.inews-item {
	clear:both;
	margin-bottom:30px;
	min-height:132px;
	position:relative;
}
	.inews-item:after,
	.inews-item:before {
		display:table;
		content:" ";
	}
	.inews-item:after {
		clear: both;
	}
.inews-item .inews-thumbnail {
	border:0;
	padding:0;
	width: 35%;
	max-width:350px;
	margin: 0 20px 0 0;
	float:left;
	position:relative;
}
	.inews-item .inews-thumbnail .inews-sticky {
		position:absolute;
		left:0; bottom:-1px;
		background-color:#74c6de;
		color:#fff;
		font-size: 11px;
		font-weight: bold;
		padding: 5px 12px;
		text-shadow:#333 0 0 1px;
	}
	.inews-item .inews-thumbnail .inews-sticky>i {
		font-size:14px;
		margin-right:8px;
	}
.inews-item .inews-date-wrapper {
	text-align: center;
	position:absolute;
	left:0;
	margin-right: 20px;
	width: 65px;
	padding-bottom: 20px;
	background-color:rgba(0,0,0,0.1);
}
.inews-item .inews-date-wrapper:before {
	content:' ';
	position:absolute;
	right:-10px;
	top:6px;
	width: 0; 
	height: 0; 
	border-top: 10px solid transparent;
	border-bottom: 10px solid transparent;

	border-left: 10px solid rgba(0,0,0,0.1);
}
	.inews-item .inews-date-wrapper .inews-date-day {
		display: block;
		font-size: 50px;
		line-height: 1;
		margin-bottom: 16px;
	}
	.inews-item .inews-date-wrapper .inews-date-month {
		display: block;
		font-size: 14px;
		line-height: 1;
		margin-bottom: 14px;
	}
	.inews-item .inews-date-wrapper .inews-date-year {
		display: block;
		font-size: 17px;
		line-height: 1;
	}
.inews-item .inews-item-content {
	overflow:hidden;
	padding-left:85px;
	position:relative;
}
	.inews-content-inner h3 {
		margin-bottom:8px;
		display:block;
		margin-top:3px;
	}
	.inews-content-inner .list-inline>li {
		padding:0;
	}

@media only screen and (max-width: 768px) {
	.inews-item {
		max-width:380px;
		margin-left:auto;
		margin-right:auto;
	}
	.inews-item .inews-thumbnail {
		float:none;
		display:block;
		margin-bottom:20px;
		width:100%;
	}
}


