}
.btn.pull-right,
.btn-group.pull-right {
  margin: 5px 0 5px 5px;
}
.btn-group .btn {
  margin-right: 0;
  margin-left: 0;
}
#header .btn,
#footer .btn,
.dropdown .btn,
