/* Button reveal */
.btn.btn-reveal {
  padding-left: 28px;
  padding-right: 28px;
  overflow: hidden;
  position: relative;
}
.btn.btn-reveal i {
  display: block;
  position: absolute;
  left: -32px;
  width: 32px;
  top: 0;
  bottom: 0;
  margin: 0;
  padding: 0;
  font-size: 17px;
  line-height: 34px;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.1);
}
.btn.btn-reveal span {
  display: inline-block;
  position: relative;
  left: 0;
}
body:not(.device-touch) .btn.btn-reveal i,
body:not(.device-touch) .btn.btn-reveal span {
  -webkit-transition: left 0.3s ease, right 0.3s ease;
  -o-transition: left 0.3s ease, right 0.3s ease;
  transition: left 0.3s ease, right 0.3s ease;
}
