	</a>


	<a class="btn btn-social-icon btn-twitter">
		<i class="fa fa-twitter"></i>
	</a>
 */
.btn-social {
  position: relative;
  padding-left: 54px;
  text-align: left;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 26px;
  margin: 0;
}
.btn-social > :first-child {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 42px;
  line-height: 40px;
  font-size: 1.6em;
  text-align: center;
  border-right: 1px solid rgba(0, 0, 0, 0.2);
