}
div.divider.divider-center:before {
  left: 0 !important;
  right: 50%;
  margin-right: 20px;
  content: '';
  position: absolute;
  top: 8px;
  height: 0;
  border-top: 1px solid #ddd;
}
div.divider.divider-center {
