	http://www.paulund.co.uk/creating-different-css3-box-shadows-effects
**************************************************************** **/
.box-shadow-1:after,
.box-shadow-1:before {
  top: 80%;
  left: 5px;
  width: 50%;
  z-index: -1;
  content: "";
  bottom: 15px;
