  min-width: 50px;
  text-align: center;
  font-size: 13px;
  margin-bottom: 3px;
  display: inline-block;
}
/* summary */
div.shop-item-summary {
  padding: 15px 8px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
}
div.shop-item-summary h2 {
  font-size: 15px;
  color: #666;
  margin: 0 0 4px 0;
}
div.shop-item-rating-line {
  display: block;
  position: relative;
}
div.shop-item-rating-line:before {
  left: 0 !important;
  right: 50%;
  margin-right: 50px;
  content: '';
  position: absolute;
  top: 10px;
  padding: 1px;
  height: 0;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
div.shop-item-rating-line:after {
  right: 0 !important;
  left: 50%;
  margin-left: 50px;
  content: '';
  position: absolute;
  top: 10px;
  padding: 1px;
  height: 0;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
