	color:#555;
}


/* BOOTSTRAP REWRITE */
.btn,
.form-control {
	height:40px;
}
.btn-sm,
.btn-xs,
.btn-lg,
.btn-xlg {
	height:auto;
}
a.btn {
	line-height:26px;
}
a.btn.btn-xs {
	line-height:inherit;
}

.btn-3d {
	line-height:25px;
	margin-bottom:3px;
}
.btn-3d.btn-xlg {
	line-height:inherit;
}



/* bootstrap modal */
.modal-backdrop {
	background-color:#fff;
}
.modal-content {
	border:0;
	border-top:#333 4px solid;
}
.modal-dialog {
	-webkit-border-radius: 3px;
	   -moz-border-radius: 3px;
			border-radius: 3px;

	-webkit-box-shadow: 0 10px 25px rgba(0, 0, 0, 0.5);
	   -moz-box-shadow: 0 10px 25px rgba(0, 0, 0, 0.5);
			box-shadow: 0 10px 25px rgba(0, 0, 0, 0.5);
}
.modal-header .close {
	margin-top:2px;
}




